@use "@carbon/styles/scss/type" as *;
@use "@carbon/styles/scss/breakpoint" as *;

.page_description p {
  @include type-style("expressive-heading-03", true);

  // 8 col
  @include breakpoint("lg") {
    width: 100%;
  }
}

// Multiple paragaphs
.page_description > * + * {
  margin-top: var(--space);
}

.page_description code {
  font-size: 0.75em;
  line-height: 1.25;
}
