@use "@carbon/react/scss/spacing" as *;

.main {
  --space: 1.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.main > * {
  margin-top: 0;
  margin-bottom: 0;
}

.main .contained {
  max-width: 1280px;
  width: 100%;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.main > * + * {
  margin-top: 1.5rem; // IE fallback
  margin-top: var(--space);
}

