@use "@carbon/react/scss/breakpoint" as *;

.container {
  // padding-left: 2rem;
  // padding-right: 2rem;
  // max-width: 1280px;
  margin: 0 auto;
  .title--grid {
    margin: 0;

    @include breakpoint("lg") {
      padding-left: calc(
        25% + 1.5rem
      ); //2rem to account for 2rem left padding default on grid
      padding-right: 2rem;
    }

    @include breakpoint("max") {
      max-width: 96.5rem;
      padding-left: 424px;
      padding-right: 0;
    }
  }
}
